.chat {
  display: flex;
  flex-direction: column;
  flex: 0.65;
}
.chat__header {
  padding: 20px;
  display: flex;
  align-items: center;
}
.chat__headerInfo {
  flex: 1;
  padding-left: 20px;
}
.chat__headerInfo > h3 {
  margin-bottom: 3px;
  font-weight: 500;
}
.chat__headerInfo > p {
  color: grey;
}

.chat__body {
  flex: 1;
  padding: 30px;
  background-color: grey;
  overflow: scroll;
}
.chat__body::-webkit-scrollbar{
  width: 0;
}
.chat__message {
  position: relative;
  font-size: 16px;
  padding: 10px;
  width: fit-content;
  border-radius: 10px;
  background-color: #ffffff;
  margin-bottom: 30px;
}
.chat__receiver {
  margin-left: auto;
  background-color: #dcf8c6;
}
.chat__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 62px;
  border-top: 1px solid lightgray;
}
.chat__footer > form {
  flex: 1;
  display: flex;
}
.chat__footer > form > input {
  flex: 1;
  border-radius: 30px;
  padding: 10px;
  border: none;
  outline: none;
}
.chat__footer > .MuiSvgIcon-root{
  padding: 10px;
  color: grey;
}
